<template>
    <nav class="navbar navbar-default navbar-static-top">
        <div class="container">
            <div class="navbar-header">
                <button type="button" class="navbar-toggle" @click="toggle()">
                    <span class="sr-only">Toggle Navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </button>
                <a href="" class="navbar-brand">
                    LHW Blog
                </a>
            </div>
            <div class="collapse navbar-collapse" :class="{'in':isShow,'':!isShow}">
                <ul class="nav navbar-nav">
                    <li><a href="/article">文章</a></li>
                    <li><a href="/article">讨论</a></li>
                </ul>
                <ul class="nav navbar-nav navbar-right">
                    <li>
                        <form role="search" method="get" action="/search" class="navbar-form navbar-right search">
                            <input type="text" name="q" placeholder="Search" required="required" class="form-control">
                        </form>
                    </li>
                    <li><a href="/login">Login In</a></li>
                    <li><a href="/register">Register</a></li>
                </ul>
            </div>
        </div>
    </nav>
</template>
<script>
export default {
  data () {
    return {
      isShow: false
    }
  },
  methods: {
    toggle () {
      this.isShow = !this.isShow
    }
  }
}
</script>



